$primary-color: rgb(21, 26, 17);
$space-between: 5px;

@mixin flexible() {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin buttonstyle-orange() {
  padding: $space-between;
  background-color: rgb(255, 156, 62);
  border-radius: 5%;
  color: $primary-color;
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(46, 49, 42);
  color: #fff;
}

a {
  text-decoration: none;
  color: $primary-color;
}

button {
  outline: none;
  border: none;
  font: inherit;
}

button:hover {
  cursor: pointer;
}

.header {
  height: 20px;
  background: #93a086;
  color: $primary-color;
  font: {weight: bold;}
  padding: $space-between * 2;
  margin-bottom: $space-between;
  @include flexible();

  .header__logo {
    color: rgb(255, 123, 0);
    font: {size: 2.0rem; weight: bold;}
    text-shadow: rgb(46, 49, 42) 1px 1px 0, rgb(46, 49, 42) -1px -1px 0,
                 rgb(46, 49, 42) -1px 1px 0, rgb(46, 49, 42) 1px -1px 0;
  }

  .header__links {
    @include flexible();

    .header__links__create {
      margin-right: $space-between;
      @include buttonstyle-orange();
    }

    .header__links__authentication-in {
      @include buttonstyle-orange();
    }
  }
}

.header__links__user-information {
  @include flexible();

  .header__links__authentication-out {
    @include buttonstyle-orange();
    margin: 0 $space-between;
  }

  .header__name {
    margin: 0 $space-between;
  }
}

.app-about {
  font: {size: 1.4rem; weight: bold;}

  .app-about__image {
    width: 800px;
    margin: 20px auto;
  }

  .app-about__image img {
    width: 100%;
  }

  .app-about__definition {
    @include flexible();
    justify-content: center;

    section {
      width: 450px;
      height: 600px;
      text-align: center;

      h3 {
        font-size: 2.5rem;
        color: rgb(255, 123, 0);
        text-decoration: underline complement($color: #fff);
      }
    }
  }

  .definition-container {
    width: 300px;
    height: 300px;
  }
}

.footer {
  @extend .header;
  height: 50px;

  a {
    color: rgb(134, 65, 1);
    font-size: 2.0rem;
  }
}

.invisible {
  visibility: hidden;
}
